import { standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Transport } from 'src/app/sdk/hr/models/transport';
import { Component, OnInit } from '@angular/core'
import { GaTransportService, GaUntukService, DirektoriVPegawaiPosisiService, DirektoriPegawaiService } from 'src/app/sdk/hr/services'
import { NgxSpinnerService } from 'ngx-spinner';
import { ActivatedRoute, Router } from '@angular/router';
import { Pegawai, V_pegawai_posisi, Untuk } from 'src/app/sdk/hr/models';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';
import { StandarApprovalService } from 'src/app/components/approval/standar/standar-approval.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss'],
})
export class DetailComponent implements OnInit {
    constructor(
        private gaTransportService: GaTransportService,
        private gaUntukService: GaUntukService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private routerActive: ActivatedRoute,
        private router: Router,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private toolsLogFlowService: ToolsLogFlowService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    id_transport: string = ''
    dataTransport: Transport = {}
    dataPegawai: Pegawai = {}
    dataPosisi: V_pegawai_posisi = {}
    dataUntuk: Untuk = {}

    ngOnInit() {
        this.routerActive.paramMap.subscribe(
            params => {
                if (params.get('id_transport')) {
                    this.id_transport = params.get('id_transport')
                }
                this.getDetailTiket()
                this.getLog()
            }
        )

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailTiket()
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getDetailTiket() {
        this.spinner.show()
        this.gaTransportService.getGaTransportsId(this.id_transport).subscribe(
            data => {
                this.dataTransport = data.data;
                this.getPegawai(data.data.id_pegawai);
                this.getPosisi(data.data.id_pegawai);
                this.getUntuk(data.data.id_untuk);

                this.params = {
                    id: data.data.id_transport,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_transport"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )
    }

    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            data => {
                this.dataPosisi = data.data;
            }
        )
    }
    getUntuk(id_untuk: string) {
        this.gaUntukService.getGaUntuksId(id_untuk).subscribe(
            data => {
                this.dataUntuk = data.data;
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_transport })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/ga/request/module/transport/list'])
    }
}
