import { Transport } from 'src/app/sdk/hr/models/transport';
import { Component, OnInit } from '@angular/core'
import { GaTransportService, GaUntukService, DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services'
import { NgxSpinnerService } from 'ngx-spinner'
import { NzNotificationService } from 'ng-zorro-antd'
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Pegawai_master, Untuk } from 'src/app/sdk/hr/models'
import { FormControl, Validators } from '@angular/forms'
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private gaTransportService: GaTransportService,
        private gaUntukService: GaUntukService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    toogleUpload = false
    dataTransport: Transport = {}

    id_transport: string = ''

    listPegawai: Pegawai_master[] = []
    listUntuk: Untuk[] = []

    ngOnInit() {
        this.id_transport = this.activatedRoute.snapshot.paramMap.get('id_transport')
        this.getPreeData();
        this.form()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'ga_transport.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_document: data })
                    this.simpan()
                }
            }
        )

    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data;
            }
        )

        this.gaUntukService.getGaUntuks().subscribe(
            data => {
                this.listUntuk = data.data;
            }
        )
    }

    getData() {
        this.spinner.show()
        this.gaTransportService.getGaTransportsId(this.id_transport).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "ga_transport",
                    for_table: "erp_ga.transport",
                    for_module_id: data.data.id_transport
                })
            }
        )
    }

    dataForm: FormGroupTypeSafe<Transport>;
    form() {
        this.dataForm = this.fb.group<Transport>(
            {
                id_pegawai: new FormControl(''),
                provider_transport: new FormControl(''),
                id_untuk: new FormControl(''),
                tanggal_request: new FormControl(''),
                kode_booking: new FormControl(''),
                kode_pembelian: new FormControl(''),
                tanggal_pembelian: new FormControl(''),
                tanggal_berangkat: new FormControl(''),
                tanggal_tiba: new FormControl(''),
                ref_perjalanan_dinas: new FormControl(''),
                harga: new FormControl(''),
                dari: new FormControl(''),
                tujuan: new FormControl(''),
                id_transport: new FormControl(''),
                id_document: new FormControl(''),
                id_user: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                id_data_flow_detail: new FormControl(''),
                id_data_flow: new FormControl(''),
                nomor: new FormControl(''),
                status: new FormControl(''),
                posisi: new FormControl(''),

            }
        )
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()

        this.dataTransport = this.dataForm.value;
        this.gaTransportService.putGaTransportsId({ id: this.id_transport, body: this.dataTransport }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )
    }

    goToBack() {

        this.router.navigate(['/ga/module/transport/list'])
    }
}
